.site-header {
    display: flex;
    border-bottom: #e1e1e1 solid 1px;

    li {
        margin: 3px 1rem 0px 0px;
    }

    .search-form {
        grid-column: 2;
        grid-row: 1;
        
        fieldset {
            margin: 0px;
            padding: 0px;
            border: none;
    
            input {
                border: none;
                background-color: transparent;
            }
        }
    
        button {
            display: none;
        }
    }
}

.menu-home-button-container-alt {
    background-image: url('http://localhost/InhabitentSite/wp-content/uploads/2020/01/inhabitent-logo-tent-white.svg');
}

.menu-home-button-container {
    grid-column: 1;
    justify-self: start;
    background-image: url('http://localhost/InhabitentSite/wp-content/uploads/2020/01/inhabitent-logo-tent.svg');
    background-size: contain;
    background-repeat: no-repeat;

    a {
        opacity: 0;
    }
}

.home-icon {
    width: 3vw;
    height: 2vw;
}

.main-navigation {
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin: 0px auto;
    padding: .5rem 0px;
    max-width: $base__width;
    align-items: center;
    overflow: hidden;
}

.menu-top-nav-container {
    justify-self:end;
    grid-column: 2;
    grid-row: 1;
    position: relative;
    left: 15px;
}

.nav-link-container {
    transition: left 1s;
    display: flex;
    position: relative;
    left: 26rem;

    &.search-bar-after {
        transition: left 1s;
        left: 11rem;
    }

    span {
        margin: .7rem 1rem 0;
        font-size: 1rem;
    }

     button {
         margin: 0px;
         padding: 0px;
         background-color: transparent;
         border: unset;
         box-shadow: unset;
     }
}